@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,400;0,700;1,400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fleur+De+Leah&display=swap);
body {
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
}

/* under construction text */
h1 {
  color: red;
  margin-top: 15%;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(25, 6, 31);
    color:rgb(216, 130, 238);
    font-weight: bold;
}

nav {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    height: 100px;
    background-color: rgb(25, 6, 31);
    color:rgb(216, 130, 238);
}

#logo {
    font-family: 'Fleur De Leah', cursive;
    text-shadow: 1px 1px 1px plum;
    font-size: 3rem;
    cursor: pointer;
}

/* #logo:hover {
    text-shadow: 1px 1px 1px lightpink;
    border-radius: 10%;
    padding: 8px;
    box-shadow: 2px 2px 1px 3px lightpink;
} */

#logo-left {
    color:rgb(240, 71, 231);
    margin-right: 1rem;
}

#logo-right {
    color: rgb(176, 83, 243);
}

input {
    border: 2px solid rgb(216, 130, 238);
}

#search-icon {
    margin-bottom: -8px;
    margin-left: 2px;
    padding: 5px;
    cursor: pointer;
}

#search-icon:hover {
    color: lightpink;
    border-radius: 10%;
    padding: 5px;
    box-shadow: 1px 1px 0.5px 1.5px lightpink; 
    margin-bottom: -8px;
    margin-left: 2px;
}

ul {
    list-style: none;
    display: flex;
    font-weight: bold;
}

li {
    margin: 0 7px;
    font-size: 1.1rem;
    padding: 7px;
    cursor: pointer;
}

li:hover {
    color: lightpink;
    border-radius: 10%;
    padding: 7px;
    box-shadow: 1px 1px 0.5px 1.5px lightpink;
}
